body{
    background-color: #ff007f;
    color:#00ced1;
    font-weight: bold;
    background-image: -webkit-gradient(linear, 0 0, 100% 0, color-stop(.5, #ffffff), color-stop(.5, transparent), to(transparent));
    margin:0;
    -webkit-background-size: 50px;
}

#media{
    position: fixed;
    bottom: 0;
}

#song{
    position: fixed;
    bottom: 0;
    right:0;
    color:black;
    width:170px;
    height:95px;
}

.kakomi{
        display: inline-flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        flex-flow: column; 
        vertical-align: top;
     
        font-size: 30px;
        line-height: 1.15em;

        border: 4px solid;
}

button{
    background: transparent; 
    border: none;
    outline: none;
}

.kakomi-text{
    width: 30px;
    height: 30px;
}

#background-effect{
    background-color: #000000;
    opacity: 0.5;
    position: fixed;
    z-index: -1;
    width: 100%;
    height: 120%;
    top: -20%;
}

#background-object-effect > div{
    color:#bce2e8;
    position: fixed;
    z-index: -1;
}

.effect-1{
    animation: effect1 3s ease-out forwards infinite;
}

.effect-2{
    animation: effect2 2s ease-out forwards infinite;
}

.effect-3{
    animation: effect3 1s ease-out forwards infinite;
}

.display-none{
    display: none;
}

#loading{
    font-size: 50px;
}

#text > div > div {
    animation: showLyrics 0.1s ease-out forwards, moveLyrics 100s infinite linear;
    display: inline-block;
}

.animation-pause{
    animation-play-state: paused !important;
}

.animation-running{
    animation-play-state: running !important;
}

#text > div {
    text-align: center;
    height: auto;
}

#text{
    position: absolute;
    top: 50%; /*親要素を起点に上から50%*/
    left: 50%;  /*親要素を起点に左から50%*/
    transform: translateY(-50%) translateX(-50%); /*要素の大きさの半分ずつを戻す*/
    -webkit-transform: translateY(-50%) translateX(-50%);
}

.noun_text {
    color: #40e0d0;
}

.text_particle {
    position: absolute;
    left: 25%;
    color: #e6b422;
    text-shadow: none;
}

.text_particle_anim_0 {
    animation: textParticle 0.5s ease-out forwards;
}

.text_particle_anim_45 {
    animation: textParticle45 0.5s ease-out forwards;
}

.text_particle_anim_225 {
    animation: textParticle225 0.5s ease-out forwards;
}

span{
    text-shadow: 
    2px  2px 1px #003366,
   -2px  2px 1px #003366,
    2px -2px 1px #003366,
   -2px -2px 1px #003366,
    2px  0px 1px #003366,
    0px  2px 1px #003366,
   -2px  0px 1px #003366,
    0px -2px 1px #003366;  
}

.fadeLyric{
    animation: fadeOut 200ms linear forwards;
}

@keyframes showLyrics {
    0% {
      font-size: 0px;
    }
    100% {
      font-size: 50px;
    }   
  }

  @keyframes moveLyrics {
    0% {
        transform: rotateZ(0deg);
    }
    50% {
        transform: rotateZ(180deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
  }

  @keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
  }

  /*真上*/
  @keyframes textParticle {
    0% {
        opacity: 0;
        font-size: 0px;
        transform: translate(0, 0%)
    }
    20% {
        opacity: 0.8;
        font-size: 30px;
        transform: translate(0, -20%)
    }
    100% {
        opacity: 0;
        font-size: 40px;
        transform: translate(0, -100%)
    }
  }

  /*45*/
  @keyframes textParticle45 {
    0% {
        opacity: 0;
        font-size: 0px;
        transform: translate(0, 0%)
    }
    20% {
        opacity: 0.8;
        font-size: 30px;
        transform: translate(-20%, -20%)
    }
    100% {
        opacity: 0;
        font-size: 40px;
        transform: translate(-100%, -100%)
    }
  }

    /*225*/
    @keyframes textParticle225 {
        0% {
            opacity: 0;
            font-size: 0px;
            transform: translate(0, 0%)
        }
        20% {
            opacity: 0.8;
            font-size: 30px;
            transform: translate(20%, 20%)
        }
        100% {
            opacity: 0;
            font-size: 40px;
            transform: translate(100%, 100%)
        }
      }

      @keyframes effect1 {
        0% {
            font-size: 300px;
            left: -20%;
            top: -20%;
            opacity: 0.7;
        }
        100% {
            opacity: 0;
            left: 50%;
            top:50%;
            font-size: 0px;
        }
      }

      @keyframes effect2 {
        0% {
            font-size: 200px;
            left: 0%;
            top: 20%;
            opacity: 0.7;
        }
        100% {
            opacity: 0;
            left: 50%;
            top:50%;
            font-size: 0px;
        }
      }

      @keyframes effect3 {
        0% {
            font-size: 100px;
            left: 80%;
            top: 10%;
            opacity: 0.7;
        }
        100% {
            opacity: 0;
            left: 50%;
            top:50%;
            font-size: 0px;
        }
      }

